.login {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 1) 100%,
        ),
        url("http://www.7elwa.com/sites/default/files/images/editor/dUyCkp.jpg");
    background-size: cover;
    position: relative;

    .logo {
        height: 40px;
    }

    .container-login {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;

        form {
            width: 300px;
            height: 300px;
            padding: 30px;
            border-radius: 5px;
            background-color: rgba(20,20,20,20);
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            input {
                height: 40px;
                border-radius: 5px;
                background-color: gray;
                color: white;
                padding-left: 10px;

                &::placeholder {
                    color: lightgray;
                }
            }
            button {
                height: 40px;
                border-radius: 5px;
                background-color: red;
                color: white;
                border: none;
                font-size: 18px;
                font-weight: 500;
                cursor: pointer;
            }

            span {
                color: lightgray;
                b {
                    color: white;
                }
            }
        }

    }

}